.headsection {
  height: 310px;
  text-align: center; }
  .headsection .face {
    display: inline-block;
    width: 100px;
    height: 100px;
    margin-top: 40px;
    border-radius: 50%;
    border: 4px solid #999;
    overflow: hidden; }
  .headsection .name {
    height: 45px;
    font-size: 24px;
    line-height: 45px; }
  .headsection .join {
    display: inline-block;
    width: 100px;
    height: 35px;
    margin-top: 20px;
    color: #fff;
    font-size: 14px;
    text-align: center;
    line-height: 35px;
    background-color: #000; }

.sectiontab {
  height: 45px;
  border-bottom: 6px solid #000;
  margin-bottom: 30px; }
  .sectiontab li {
    float: left;
    width: 600px;
    color: #999;
    font-size: 16px;
    text-align: center; }
    .sectiontab li.active {
      color: #000; }
  .sectiontab li + li:before {
    position: absolute;
    content: "";
    height: 23px;
    margin-left: -285px;
    border-left: 1px solid #999; }

.maincontent ul {
  float: left;
  width: 280px; }
  .maincontent ul li {
    width: 280px;
    margin-bottom: 30px;
    border: 1px solid #999; }
    .maincontent ul li h3 {
      text-indent: 15px;
      font-size: 14px;
      line-height: 35px; }
    .maincontent ul li .bottomsection {
      font-size: 12px;
      padding: 15px; }
      .maincontent ul li .bottomsection .focus {
        float: right; }
.maincontent ul + ul {
  margin-left: 26px; }
